.main-section {
  background: linear-gradient(-9.82deg, rgb(0, 15, 22) 28.115%,rgb(1, 45, 65) 59.23%,rgb(4, 59, 88) 93.412%,rgb(3, 9, 69) 112.256%);
  padding: 14.25rem 0 34.375rem;
  color: #FDFDFD;
  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -0.3125rem;
    width: calc(100% + 0.3125rem);
    height: 40rem;
    background: #fff;
    clip-path: polygon(100% 100%, 0 100%, 100% 0);
    pointer-events: none;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 2rem;
  }
  &__left {
    position: relative;
    z-index: 1;
    max-width: 48.75rem;
  }
  &__right {
    .main-section__img {
      display: block;
    }
  }
  &__group {
    p {
      max-width: 33rem;
      font-family: "Mulish";
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 700;
      margin: 2.5rem 0 0 0;
      span {
        color: rgb(75, 214, 231);
      }
    }
  }
  h1 {
    font-size: 3rem;
    line-height: 130%;
    font-weight: 400;
    text-transform: uppercase;
    span {
      border-bottom: 0.0625rem solid #FDFDFD;
    }
    strong {
      font-weight: 700;
      color: rgb(75, 214, 231);
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.25rem / 2));
    gap: 1.25rem;
    margin-top: 4rem;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 1.25rem;
    i {
      width: 5.5rem;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    p {
      font-size: 1.125rem;
      line-height: 130%;
      margin: 0;
      strong {
        font-weight: 700;
      }
    }
  }
  &__group2 {
    margin-top: 5.875rem;
  }
  &__subtitle {
    font-size: 1.5rem;
    line-height: 130%;
    strong {
      font-weight: 700;
    }
  }
  &__flex {
    display: flex;
    align-items: center;
    gap: 3.125rem;
    margin-top: 3.125rem;
  }
  &__btn {
    flex-shrink: 0;
  }
  &__info {
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
  }
  &__items2 {
    max-width: 15rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 3.125rem;
    padding-bottom: 3.75rem;
  }
  &__item2 {
    p:nth-child(1) {
      font-family: "Mulish";
      font-size: 2rem;
      line-height: 130%;
      font-weight: 700;
      margin: 0;
      span {
        font-size: 2.5rem;
      }
    }
    p:nth-child(2) {
      font-size: 1rem;
      line-height: 130%;
      margin: 0.5rem 0 0 0;
    }
  }
  &__img {
    position: absolute;
    top: -14rem;
    right: -4.5rem;
    max-width: 82.1875rem;
    width: 68.5vw;
    display: none;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.advantages {
  z-index: 3;
  .container-fluid {
    z-index: 1;
  }
  h2 {
    max-width: 45.625rem;
    text-align: center;
    font-weight: 500;
    margin: 0 auto;
    .text_color {
      font-weight: 700;
      color: rgb(8, 82, 122);
    }
  }
  p {
    max-width: 45.625rem;
    text-align: center;
    font-family: "Mulish";
    font-size: 1.25rem;
    line-height: 130%;
    color: #0C0C0C;
    margin: 1.25rem auto 0;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    padding: 0 2.5rem 0;
    margin-top: 13.625rem;
  }
  &__left {
    .advantages__item {
      &:nth-child(even) {
        margin-left: 6.125rem;
      }
      &:last-child {
        margin-left: 3.125rem;
      }
    }
  }
  &__center {
    padding-top: 9.375rem;
  }
  &__right {
    .advantages__items {
      gap: 9.375rem;
    }
    .advantages__item {
      margin-left: auto;
      &:nth-child(even) {
        margin-right: 3.125rem;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 6.625rem;
  }
  &__item {
    position: relative;
    max-width: 30rem;
    display: flex;
    gap: 1.125rem;
    background: linear-gradient(180deg, rgba(228,228,228,1) 0%, rgba(255,255,255,1) 100%);
    border-radius: 1.875rem;
    padding: 1.25rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.125rem);
      height: calc(100% - 0.125rem);
      background: #fff;
      border-radius: 1.875rem;
      pointer-events: none;
    }
    i {
      position: relative;
      z-index: 1;
      width: 2.375rem;
      height: 2.375rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: linear-gradient(135.00deg, rgb(14, 166, 235) 0%,rgb(53, 87, 105) 100%);
      border-radius: 50%;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: calc(100% + 0.75rem);
        height: calc(100% + 0.75rem);
        background: linear-gradient(135.00deg, rgb(14, 166, 235) 0%,rgb(53, 87, 105) 100%);
        border-radius: 50%;
        opacity: 0.1;
        pointer-events: none;
      }
      img {
        width: 0.875rem;
      }
    }
    p {
      position: relative;
      z-index: 1;
      text-align: start;
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 700;
      color: #00293F;
      margin: 0;
    }
  }
  &__video {
    display: flex;
    flex-direction: column;
    align-items: center;
    &-text {
      position: relative;
      font-size: 1rem;
      line-height: 130%;
      color: #0C0C0C;
      i {
        position: absolute;
        top: calc(100% + 0.5rem);
        right: -1rem;
        width: 1rem;
        pointer-events: none;
        img {
          width: 100%;
        }
      }
    }
    &-btn {
      position: relative;
      width: 7.8125rem;
      height: 7.8125rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      cursor: pointer;
      margin-top: 3.125rem;
      &:after {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgb(0, 41, 63);
        border-radius: 50%;
        pointer-events: none;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(135.00deg, rgb(14, 166, 235) 0%,rgb(53, 87, 105) 100%);
        border-radius: 50%;
        pointer-events: none;
      }
      &:hover {
        &:before {
          background: rgb(53, 87, 105);
        }
      }
      img {
        position: relative;
        z-index: 1;
        width: 0.9375rem;
      }
    }
  }
  &__group {
    max-width: 23.125rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 2.8125rem;
    margin: -8rem auto 0;
  }
  &__info {
    position: relative;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    color: #0C0C0C;
    i {
      position: absolute;
      top: 100%;
      right: -2rem;
      width: 4.125rem;
      img {
        width: 100%;
      }
    }
  }
  &__bottom {
    .advantages__video {
      display: none;
    }
  }
  &__img {
    position: absolute;
    left: 50%;
    bottom: 5.5rem;
    transform: translate(-50%, 0);
    width: 100%;
    min-width: 125rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
