  }
  &:nth-child(2) {
    grid-column: 4/7;
  }
  &:nth-child(3) {
    grid-column: 7/10;
  }
  &:nth-child(4) {
    grid-column: 10/13;
  }
  &:nth-child(5) {
    grid-column: 1/5;
    grid-row: 2;
  }
  &:nth-child(6) {
    grid-column: 5/9;
    grid-row: 2;
  }
  &:nth-child(7) {
