      width: 0.75rem;
      height: 0.75rem;
      background: #fff;
      opacity: 1;
      margin: 0 !important;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1.375rem;
        height: 1.375rem;
        background: rgba(255, 255, 255, 0.2);
