.footer {
  color: #FDFDFD;
}
.footer-top {
  border-top: 0.0625rem solid rgba(255, 255, 255, 0.2);
  border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.2);
  padding: 2.8125rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8.75rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 1.875rem;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.875rem;
  }
  &__logo {
    width: 8.25rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 0.875rem;
    line-height: 150%;
  }
  &__location {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    p {
      display: flex;
      gap: 0.3125rem;
      font-size: 0.875rem;
      line-height: 130%;
      margin: 0;
    }
    strong {
      min-width: 10.3125rem;
      max-width: 10.3125rem;
      font-weight: 600;
    }
    span {
      max-width: 10.3125rem;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 6.25rem;
  }
  &__email {
    font-size: 0.875rem;
    line-height: 130%;
    border-bottom: 0.0625rem solid #FDFDFD !important;
    padding-bottom: 0.1875rem;
    &:hover {
      color: rgb(75, 214, 231);
      border-color: rgb(75, 214, 231) !important;
    }
  }
  &__phone {
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: rgb(75, 214, 231);
    }
  }
}
.footer-bottom {
  padding: 2rem 0;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1rem 2rem;
  }
  &__left {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1rem 10.3125rem;
  }
  &__right {
    .footer-bottom__text {
      gap: 1rem 3.75rem;
    }
  }
  .b2b {
    width: 8.75rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    p {
      font-size: 0.65rem;
      line-height: 130%;
      margin: 0;
      a:hover {
        color: rgb(75, 214, 231);
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .footer-top {
    padding: 2.5rem 0;
    &__inner {
      gap: 6.25rem;
    }
    &__group {
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    &__group {
      flex-direction: column;
      align-items: start;
      gap: 0.625rem;
    }
  }
  .footer-bottom {
    &__left {
      gap: 1rem 3.75rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .footer-top {
    &__inner {
      gap: 1.875rem;
    }
    &__right {
      flex-grow: unset;
    }
    &__text {
      font-size: 0.875rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    &__left {
      flex-direction: column;
      align-items: start;
      gap: 0.625rem;
    }
    &__right {
      flex-shrink: 0;
    }
    &__phone {
      font-size: 1rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__left {
      flex-direction: row;
      align-items: center;
      gap: 1.5rem;
    }
    &__right {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__left {
      flex-direction: column;
      align-items: start;
      gap: 0.625rem;
    }
    &__right {
      flex-direction: column;
      align-items: start;
    }
  }
}