h2 {
  text-transform: uppercase;
  font-size: 2.5rem;
  line-height: 130%;
  font-weight: 700;
  .text_color {
    color: rgb(75, 214, 231);
  }
}
.btn-blue {
  background-image: url("../img/btn-blue__bg.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: 0 0.25rem 0.3125rem 0 rgba(0, 0, 0, 0.12);
  border-radius: 1.5rem;
  &:hover {
    background-image: url("../img/btn-blue__bg2.svg");
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    left: 50%;
    bottom: -0.1875rem;
    transform: translate(-50%, 0);
    width: calc(100% - 3.75rem);
    height: 2.5rem;
    background: rgb(54, 195, 255);
    filter: blur(1.25rem);
    pointer-events: none;
  }
  span {
    position: relative;
    z-index: 1;
    min-width: unset;
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 600;
    color: #FFFFFF;
    padding: 1.125rem 2rem;
  }
}
.left-anim {
  transform: translate(-40%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(40%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.navigation {
  padding: 3.125rem 0;
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    li {
      font-size: 1.5rem;
      line-height: 130%;
      a:hover {
        color: rgb(75, 214, 231);
      }
    }
  }
}
.popup {
  background: transparent;
  padding: 0;
  .form-wrap {
    background: linear-gradient(180deg, rgba(0, 0, 0), rgba(0, 0, 0, 0) 100%);
  }
}


@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.75rem;
  }
}