#header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(4, 4, 4, 0.2);
  backdrop-filter: blur(1.25rem);
  padding: 0.9375rem 0;
  color: #FDFDFD;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  .header__left {
    display: flex;
    align-items: center;
    gap: 1.875rem;
  }
  .header__right {
    display: flex;
    align-items: center;
    gap: 12.5rem;
  }
  .header__logo {
    width: 8.25rem;
    display: block;
    img {
      width: 100%;
    }
  }
  .header__text {
    font-size: 0.875rem;
    line-height: 150%;
  }
  .header__location {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    p {
      display: flex;
      gap: 0.3125rem;
      font-size: 0.875rem;
      line-height: 130%;
      margin: 0;
    }
    strong {
      min-width: 10.3125rem;
      max-width: 10.3125rem;
      font-weight: 600;
    }
    span {
      max-width: 10.3125rem;
    }
  }
  .header__wrap {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .header__group {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .header__email {
    font-size: 0.875rem;
    line-height: 130%;
    border-bottom: 0.0625rem solid #FDFDFD !important;
    padding-bottom: 0.1875rem;
    &:hover {
      color: rgb(75, 214, 231);
      border-color: rgb(75, 214, 231) !important;
    }
  }
  .header__phone {
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: rgb(75, 214, 231);
    }
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 2.5rem;
  height: 1.5625rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: #FDFDFD;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      transform: rotate(-45deg);
      top: 0.625rem;
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      transform: rotate(45deg);
      bottom: 0.625rem;
    }
  }
}

@media screen and (max-width: 1800px) {
  #header {
    .header__right {
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  #header {
    .header__group {
      flex-direction: column;
      align-items: start;
      gap: 0.625rem;
    }
  }
}

@media screen and (max-width: 1300px) {
  #header {
    .header__left {
      flex-direction: column;
      align-items: start;
      gap: 0.625rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  #header {
    .header__inner {
      gap: 1.5rem;
    }
    .header__right {
      flex-shrink: 0;
      gap: 1.5rem;
    }
    .header__wrap {
      gap: 1.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    .header__left {
      flex-direction: row;
      align-items: center;
      gap: 1.5rem;
    }
    .header__right {
      position: fixed;
      z-index: 12;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      flex-direction: column;
      gap: 2rem;
      background: #00293F;
      padding: 9.375rem 1rem 3.125rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    .header__wrap {
      flex-direction: column;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 499px) {
  #header {
    .header__left {
      flex-direction: column;
      align-items: start;
      gap: 0.625rem;
    }
  }
}