html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}

body {
  font-family: "Open Sans", sans-serif;
  zoom: var(--zoom);
}

.fixed-body {
  overflow: hidden;
}

.page-frame {
  position: relative;
  overflow: hidden;
}

.container-fluid {
  max-width: 1812px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Mulish";
  font-weight: 700;
  margin: 0;
}

img {
  object-fit: cover;
}